.reasons {
  margin-bottom: 27px;

  @media (min-width: $tablet-width) {
    margin-bottom: 45px;
  }

  @media (min-width: $desktop-content-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 80px;
    row-gap: 50px;
    min-height: 374px;
    margin-bottom: 71px;
  }
}


.reasons__item {
  position: relative;
  background-color: $color_basic_grey;
  margin-bottom: 20px;
  padding: 30px 20px 19px 20px;

  &:last-child {
    margin-bottom: 0;
  }

  @media (min-width: $tablet-width) {
    padding: 77px 62px 59px 52px;
    margin-bottom: 30px;
  }

  @media (min-width: $desktop-content-width) {
    height: 100%;
    padding: 47px 51px 31px 52px;
  }
}

.reasons__title {
  font-weight: 400;
  font-size: $font_size_title_small;
  line-height: $line_height_title;
  color: $color_special_black;
  text-transform: uppercase;
  margin-top: 0;
  margin-bottom: 30px;
  margin-left: 1px;
  padding-left: 70px;

  &::before {
    @include reset_pseudo-element-style;
    top: 21px;
    left: 22px;
    background-image: url("../img/mobile/cat_slim.svg");
    width: 50px;
    height: 53px;
  }

  &--weight-gain {
    &::before {
      background-image: url("../img/mobile/cat_pro.svg");
      width: 67px;
      height: 50px;
      top: 24px;
      left: 13px;
    }
  }

  @media (min-width: $tablet-width) {
    font-size: $font_size_title;
    margin-bottom: 25px;
    padding-left: 0;

    &::before {
      background-image: url("../img/tablet/cat_slim.svg");
      right: 62px;
      left: auto;
      top: 81px;
      width: 200px;
      height: 211px;
    }

    &--weight-gain {
      &::before {
        background-image: url("../img/tablet/cat_pro.svg");
        right: 28px;
        left: auto;
        top: 91px;
        width: 268px;
        height: 200px;
      }
    }
  }

  @media (min-width: $desktop-content-width) {
    text-align: center;
    padding-left: 20px;
    padding-top: 30px;

    &::before {
      background-image: url("../img/desktop/cat_slim.svg");
      right: auto;
      left: 52px;
      top: 41px;
      width: 100px;
      height: 110px;
    }

    &--weight-gain {
      padding-left: 61px;

      &::before {
        background-image: url("../img/desktop/cat_pro.svg");
        left: 33px;
        top: 47px;
        width: 268px;
        height: 200px;
      }
    }
  }
}

.reasons__text {
  font-family: $font_second, $font_family_reserve;
  font-size: $font_size_base;
  line-height: $line_height_base;
  color: $color_text-dark-grey;
  margin-bottom: 17px;
  padding-bottom: 22px;
  border-bottom: 1px solid $color_border-link;


  @media (min-width: $tablet-width) {
    font-size: $font_size_medium;
    line-height: $line_height_title_small;
    margin-right: 220px;
    margin-bottom: 29px;
    border: none;

    &--weight-gain {
      margin-right: 254px;
    }
  }

  @media (min-width: $desktop-content-width) {
    margin-top: 74px;
    margin-right: 0;
    margin-bottom: 3px;
  }
}

.reasons__link-item {
  font-size: $font_size_medium;
  line-height: $line_height_middle;
  text-decoration: none;
  color: $color_basic_black;
  text-transform: uppercase;
  position: relative;
  left: -1px;
  display: inline-block;
  width: 130px;
  margin-bottom: -3px;

  &::before {
    @include reset_pseudo-element-style;
    right: -4px;
    top: 2px;
    width: 32px;
    height: 12px;
    background-image: url("../img/icon_arrow.svg");
  }

  &--weight-gain {
    &::before {
      right: -2px;
    }
  }

  &:hover {
    &::before {
      background-image: url("../img/icon_arrow_hover.svg");
    }
  }

  &:active {
    color: rgba($color_basic_black, 0.3);
    fill: rgba($color_basic_black, 0.3);

    &::before {
      background-image: url("../img/icon_arrow_active.svg");
    }
  }

  @media (min-width: $tablet-width) {
    font-size: $font_size_middle;
    line-height: 30px;
    left: 0;

    &::before {
      right: -35px;
      top: 9px;
    }

    &--weight-gain {
      &::before {
        right: -31px;
      }
    }
  }

  @media (min-width: $desktop-content-width) {
    &::before {
      right: -38px;
      top: 8px;
    }

    &--weight-gain {
      &::before {
        right: -32px;
      }
    }
  }
}
