.show-more {
  position: relative;
  height: 100%;
  padding: 0 20px 14px 20px;

  @media (min-width: $tablet-width) {
    border: 2px solid $color_basic_grey;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding: 40px 30px;
    height: 100%;
  }

  @media (min-width: $desktop-content-width) {
    border: 2px solid $color_basic_grey;
  }
}

.show-more__link {
  background-color: $color_basic_grey;
  color: $color_text-dark-grey;
  text-decoration: none;
  width: 100%;
  margin: 0 auto;
  margin-top: auto;
  margin-bottom: 10px;

  @media (min-width: $tablet-width) {
    width: 249px;
    margin-bottom: -2px;
  }

  @media (min-width: $desktop-content-width) {
    width: 171px;
  }

  &:hover {
    background-color: $color_hover_grey;
  }

  &:active {
    background-color: $color_hover_grey;
    color: rgba($color_text-dark-grey, 0.3);
  }
}

.show-more__title {
  font-family: $font, $font_family_reserve;
  font-size: $font_size_medium;
  line-height: $line_height_medium;
  font-weight: 400;
  color: $color_special_black;
  text-transform: uppercase;
  text-align: center;
  position: relative;
  padding-top: 63px;
  margin-bottom: 7px;

  &::before {
    content: "";
    position: absolute;
    width: 30px;
    height: 2px;
    border-bottom: 2px solid $color_plus_grey;
    left: 50%;
    top: 23px;
    margin-left: -15px;
  }

  &::after {
    content: "";
    position: absolute;
    width: 2px;
    height: 30px;
    border-left: 2px solid $color_plus_grey;
    left: 50%;
    top: 9px;
  }

  @media (min-width: $tablet-width) {
    max-width: 140px;
    padding-top: 0;
    margin: 119px auto 24px auto;
    font-size: $font_size_middle;
    line-height: 22px;

    &::before {
      width: 60px;
      height: 2px;
      border-bottom: 2px solid $color_plus_grey;
      left: 38px;
      top: -79px;
      margin-left: 0;
    }

    &::after {
      content: "";
      position: absolute;
      width: 2px;
      height: 60px;
      border-left: 2px solid $color_plus_grey;
      left: 68px;
      top: -109px;
    }
  }

  @media (min-width: $desktop-content-width) {
    &::before {
      left: 39px;
    }

    &::after {
      left: 69px;
    }
  }
}


.show-more__desc {
  font-family: $font_second, $font_family_reserve;
  font-size: $font_size_small;
  line-height: $line_height_middle;
  color: $color_text-dark-grey;
  display: block;
  text-align: center;
  margin: 0 0 21px 0;
  align-self: center;

  @media (min-width: $tablet-width) {
    font-size: $font_size_medium;
    line-height: $line_height_base;
    width: 167px;
    margin: 0 auto;
  }
}
