.range {
  margin: 0 auto;

  @media (min-width: $tablet-width) {
    min-height: 526px;
  }

  @media (min-width: $desktop-content-width) {
    margin: 0;
    display: flex;
    align-items: center;
  }
}

.range__wrapper {
  margin-bottom: 27px;
  min-height: 232px;
  text-align: center;

  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: 708px;
    justify-content: center;
    margin-bottom: 100px;
    position: relative;
  }

  @media (min-width: $desktop-content-width) {
    position: relative;
    grid-template-rows: 1fr;
    grid-template-columns: 690px;
  }
}

.range__picture {
  display: block;
  max-width: 280px;

  &--noshow {
    display: none;
  }

  &--show {
    display: block;
  }

  @media (min-width: $tablet-width) {
    max-width: 677px;
  }
}

.range__image {
  min-height: 240px;
}

.range__placeholder {
  display: flex;
  justify-content: center;
  position: relative;
  top: -7px;
  left: -3px;

  @media (min-width: $tablet-width) {
    top: -107px;
    left: 0;
  }

  @media (min-width: $desktop-content-width) {
    top: -25px;
    left: 32px;
  }
}

.range__button {
  display: block;
  border: none;
  background-color: inherit;
  font-family: inherit;
  font-size: $font_size_medium;
  line-height: $line_height_title_median;
  text-transform: uppercase;
  color: $color_basic_black;
  cursor: pointer;
  margin: 0;
  padding: 0;
  outline-offset: 3px;
  position: relative;

  &--before {
    left: -16px;
  }

  &--after {
    left: 20px;
  }

  &--disabled {
    pointer-events: none;
    cursor: default;
    opacity: 0.3;
    border: none;
  }

  &:hover {
    border-bottom: 1px solid $color_basic_black;
  }

  &:active {
    border-bottom: 1px solid transparent;
  }

  @media (min-width: $tablet-width) {
    font-size: $font_size_middle;
    border-bottom: 1px dotted $color_frames_grey;

    &--before {
      left: -23px;
    }

    &--after {
      left: 23px;
    }
  }
}

.range__control {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  bottom: -14px;

  @media (min-width: $tablet-width) {
    justify-self: center;
    grid-column: span 2;
    width: auto;
    position: absolute;
    bottom: 66px;
  }

  @media (min-width: $desktop-content-width) {
    width: auto;
    z-index: 1;
    left: 20.5%;
    bottom: -35px;
  }
}

.range__input {
  background-color: $color_basic_white;
  padding: 3px 6px 4px 6px;
  width: 83px;
  height: 14px;
  border: 1px solid $color_frames_grey;
  border-radius: 6px;
  position: relative;
  left: 3px;

  @media (min-width: $tablet-width) {
    position: relative;
    width: 427px;
    height: 6px;
    background-color: $color_range_grey;
    border: 0;
    border-radius: 6px;
  }
}

.range__toggle {
  width: 35px;
  height: 6px;
  background-color: $color_basic_green;
  border: none;
  border-radius: 6px;
  position: absolute;
  top: 3px;
  left: 4px;

  &:hover {
    box-shadow: 0 3px 5px 2px rgba(34, 60, 80, 0.19);
  }

  &:active {
    box-shadow: 0 3px 5px 2px rgba(34, 60, 80, 0.2);
    border: 1px solid $color_basic_green;
  }

  @media (max-width: $mobile-width-only) {
    &--medium {
      left: 23px;
    }

    &--after {
      left: 42px;
    }
  }

  @media (min-width: $tablet-width) {
    left: 46%;
    top: -13px;
    border-radius: 50%;
    background-color: $color_basic_white;
    border: 1px solid $color_frames_grey;
    width: 34px;
    height: 34px;

    &::before {
      position: absolute;
      content: "";
      background-color: $color_basic_green;
      width: 8px;
      height: 8px;
      top: 12px;
      left: 12px;
      border-radius: 50%;
    }
  }
}
